<template>
  
  <center>
    <div>
      
      <TABLE BORDER=1 CELLSPACING=1 CELLPADDING = 1>
        <tr>
          <th width=200 bgcolor=#ffeded>제목</th>
          <th width=100 bgcolor=#ffeded>글쓴이</th>
          <th width=150 bgcolor=#ffeded>날짜</th>
          <th width=100 bgcolor=#ffeded>조회수</th>
        </tr>
        <tr v-for="qnaItem in qnalist" v-bind:key="qnaItem.num" >
        <td align=center>&nbsp;
          <span v-show="qnaItem.comment != null" style="font-weight:bold; color:blue; font-size:5px"> [답변완료]</span>
          <span style="text-align:center;" @click="getQna(qnaItem.num)">{{qnaItem.title}}</span>
          <span v-show="qnaItem.wdate.substring(0, 10) == new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate()" style="color:red; font-size:3px">
          <img src="resources/img/new.jpg" alt="Image" class="img-fluid"/>
