        </td>
          
        <td align=center>&nbsp;{{qnaItem.name}}</td>
          
        <td align=center>&nbsp;{{qnaItem.wdate.substring(0, 10)}}</td>
          
        <td align=center>&nbsp;{{qnaItem.count}}</td>
          
        </tr>
    </table>
    </div>
  </center>
</template>

<script>
import Constant from "../Constant";

export default {
  created() {
    this.$store.dispatch(Constant.GET_QNALIST); //action call
  },
  computed: {
    //store안의 qnalist check
    qnalist() {
      return this.$store.state.qnalist;
    }
  },
  methods: {
    removeQna: function(num) {
      this.$store.dispatch(Constant.REMOVE_QNA, { num });
    },
    getQna: function(num) {
      this.$router.push("/detail/" + num);
    }
  }
};
</script>

